.wrapper{
    width: 100%;
    max-height: min((100vh - 96px) - 60px,734px);
    min-height: 100px;
    background: rgb(255, 255, 255);
    box-shadow: rgb(0 0 0 / 12%) 0 2px 12px;
    border-radius: 8px;
    padding-top: 8px;
    position: relative;   
    display: flex;
    flex-direction: column;
}