@import 'normalize.css';


// Css co ban
:root{
    --primary:#fe2c55;
    --text-color:#161823;
    --black:#000;
    --white:#fff;

    --font-family:'ProximaNova', sans-serif;

    //default layout
    --default-layout-header-height:60px;
    --default-layout-width:1150px;  
    --default-layout-horizontal-spacer:24px;
    
}
html{
    font-size: 62.5%;
}
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

// font
@font-face {
    font-family:SofiaPro;
    src: url('/assets/fonts/Proxima-Nova-Bold.woff2');
    font-weight: 700;
}
@font-face {
    font-family:ProximaNova;
    src: url('/assets/fonts/sofiapro-bold.woff2');
    font-weight: 700;
}
@font-face {
    font-family:ProximaNova;
    src: url('/assets/fonts/Proxima-Nova-Bold.woff2');
    font-weight: 600;
}
@font-face {
    font-family:ProximaNova;
    src: url('/assets/fonts/Proxima-Nova-Regular.woff2');
    font-weight: 400;
}

body{
    font-family: var(--font-family);
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    color: var(--text-color);   
    overflow-y: overlay;

    .tippy-box{
        background-color: rgba(84, 84, 84, 0.92);
        font-size: 1.6rem;
        font-weight: 800;
        border-radius: 8px;
        line-height: 2.2rem;
    }

    .tippy-box[data-placement^=bottom]>.tippy-arrow:before{
        color: rgba(84, 84, 84, 0.92);
        top: -8 px;
    }

    .tippy-content{
        padding: 8px 9px;
    }


}
button, input,[tabindex]{
    outline: none;
    border: none;
}

a[href]{
    color: var(--text-color);
    text-decoration: none;
}

//Srollbar css
html *::-webkit-scrollbar {
    border-radius: 0;
    width: 8px;
}
html *::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(22, 24, 35, .1);
}

html *::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(0, 0, 0, 0);
}
